<template>
  <div class="page_container">
    <nav-header-banner title="" />
    <!--    <van-nav-bar title="" left-text="" left-arrow @click-left="$router.back()" />-->
    <div class="page_body">
      <DeptSelectBar @areaClick="areaChange" />
      <div class="list_contact">
        <div v-for="(item,index) in userList" :key="index" class="list_contact_item">
          <div class="list_contact_item_group">
            <img class="list_contact_item_head_img" :src="imgURL+ item.oort_uuid">
            <span>{{ item.oort_name }}</span>
          </div>
          <div>
            <van-button size="mini" @click="authUser(item)"></van-button>
          </div>
        </div>
        <div v-for="(item,index) in deptList" :key="index +item.oort_dcode" class="list_contact_item" @click="deptClick(item)">
          <div class="list_contact_item_group">
            <img v-if="item.oort_photo" class="list_contact_item_head_img" :src="item.oort_photo">
            <img v-else class="list_contact_item_head_img" src="@/assets/img/tx.png">
            <span>{{ item.oort_dname }}</span>
          </div>
          <van-icon name="arrow" />
        </div>
      </div>
    </div>
    <van-popup v-model:show="show" position="bottom">
      <van-picker
        title=""
        show-toolbar
        :columns-field-names="{text: 'name'}"
        :columns="roleList"
        @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
import DeptSelectBar from '../components/deptSelectBar.vue'
import Config from '@/config/index.js'
import bus from '@/util/bus.js'
import { Dialog, Toast } from 'vant'
export default {
  name: 'SelfMeunAndAuthLogin',
  components: { NavHeaderBanner, DeptSelectBar },
  data() {
    return {
      roleList: [],
      result: '',
      parentDeptCode: '',
      deptList: [],
      userList: [],
      show: false,
      currentUser: null,
      imgURL: Config.URL + 'oort/oortcloud-sso/photo/'
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.getDeptUser()
      this.getAllAuth()
    },
    onConfirm(item) {
      const data = {}
      data.accessToken = Config.token
      data.ruuid = item.ruuid
      data.uuid = this.currentUser.oort_uuid
      let url = Config.URL + 'oort/oortcloud-auth/' + 'admin/v1/role.userlike'
      const params = {
        data: data,
        url: url,
        method: 'post'
      }
      window.androidFunUtils.http(params).then(
        (res) => {
          if (res.code === 200) {
            this.show = false
            Toast.success('')
          } else {
            Toast.fail('error' + res.code + res.msg)
          }
        },
        (reject) => {
          Toast.fail('error', reject)
        }
      )
    },
    getAllAuth() {
      const data = {}
      data.accessToken = Config.token
      data.page = 1
      data.pagesize = 999
      data.showUser = 1
      let url = Config.URL + 'oort/oortcloud-auth/' + 'admin/v1/role.list'
      const params = {
        data: data,
        url: url,
        method: 'post'
      }
      window.androidFunUtils.http(params).then(
        (res) => {
          if (res.code === 200) {
            this.roleList = res.data.list
          }
        },
        (reject) => {
          alert(reject)
        }
      )
    },
    deptClick(data) {
      bus.$emit('addArea', data)
    },
    authUser(data) {
      this.currentUser = data
      this.show = true
    },
    getDeptUser() {
      const data = {}
      data.accessToken = Config.token
      data.oort_depcode = this.parentDeptCode
      data.page = 1
      data.pagesize = 999
      data.showUser = 1
      let url = Config.URL + 'oort/oortcloud-sso/' + 'sso/v1/getdeptuser'
      const params = {
        data: data,
        url: url,
        method: 'post'
      }
      window.androidFunUtils.http(params).then(
        (res) => {
          if (res.code === 200) {
            this.userList = res.data.user || []
            this.deptList = res.data.dept || []
          }
        },
        (reject) => {
          alert(reject)
        }
      )
    },
    areaChange(data) {
      this.parentDeptCode = data.oort_dcode
      this.getDeptUser()
    }
  }
}
</script>

<style lang="less">
    .page_container {
        width: 100%;
        height: 100%;
    }
    .page_body {
        width: 100%;
        height: calc(100vh - 48px);
        overflow: auto;
    }
    .list_contact {
        display: flex;
        flex-direction: column;
    }

    .list_contact_item {
        cursor: pointer;
        display: flex;
        flex-direction: row;
        align-items: center;
        border-bottom: 0.5px solid #f7f7f7;
        height: 48px;
        width: 96%;
        margin: 0 auto;
        justify-content: space-between;
        span {
            font-size: 16px;
            color: #333;
        }
        &_head_img {
            margin: 0 12px;
            width: 32px;
            height: 32px;
            border-radius: 50%;
        }
        &_group {
            display: flex;
            align-items: center;
        }
    }
</style>

