<template>
  <div>
    <nav-header-banner title="" />
    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick">App</van-button>
    </div>
    <span></span>
    <div>
      <span>{{ result }}</span>
    </div>
    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick1">IMUSERID</van-button>
    </div>
    <span></span>
    <div>
      <span>{{ result1 }}</span>
    </div>
    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick2"></van-button>
    </div>
    <span></span>
    <div>
      <!-- <span>{{ result2 }}</span> -->
    </div>
  </div>
</template>
<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
export default {
  name: 'AppversionPage',
  components: { NavHeaderBanner },
  data() {
    return {
      result: '',
      result1: '',
      result2: '',
    }
  },
  methods: {
    nfcClick() {
      window.androidFunUtils.getAppVersion().then((res) => {
        console.log(res)
        this.result = res
      })
    },
    nfcClick1() {
      window.androidFunUtils.getUserId().then((res) => {
        console.log(res)
        this.result1 = res
      })
    },
    nfcClick2() {
      window.androidFunUtils.getTaskInfo().then((res) => {
        console.log( res)
        console.log(JSON.parse(res));
        this.result2 = res
      })
    },
  },
}
</script>

