<template>
  <div>
    <nav-header-banner title="OCR" />
    <!--    <van-nav-bar left-text="" left-arrow @click-left="$router.back()" />-->
    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick">OCR</van-button>
    </div>
    <span></span>
    <div>
      <span>{{ result }}</span>
    </div>
  </div>
</template>
<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
export default {
  name: 'OCRPage',
  components: { NavHeaderBanner },
  data() {
    return {
      result: ''
    }
  },
  methods: {
    nfcClick() {
      window.androidFunUtils.OcrFunc().then((res) => {
        console.log(res)
        this.result = res
      })
    }
  }
}
</script>

