<template>
  <img
    v-if="imgSrc"
    :src="imgSrc"
    v-bind="$attrs"
    @error="imgLoadError"
    v-on="$listeners"
  >
</template>

<script>
import Config from '@/config'
export default {
  name: 'OortImage',
  props: {
    src: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      imgSrc: ''
    }
  },
  created() {
    this.imgInit()
  },
  methods: {
    imgInit() {
      let imgUrl
      if (this.src) {
        if (this.src.includes('http')) {
          let data = this.src
          let index = data.lastIndexOf('oort/oortwj1/')
          imgUrl = data.substring(index, data.length)
          this.imgSrc = Config.URL + imgUrl
        } else {
          if (this.src.includes('oort/oortwj1')) {
            this.imgSrc = Config.URL + this.src
          } else {
            this.imgSrc = Config.URL + 'oort/oortwj1' + this.src
          }
        }
      }
    },
    imgLoadError() {
      console.error('', this.imgSrc)
    }
  }
}

</script>

