<template>
  <div>
    <nav-header-banner title="" />
    <div style="
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 18px;
      ">
      <van-button type="primary" @click="nfcClick"></van-button>
    </div>
    <div>
      {{ idCardObject }}
    </div>
  </div>
</template>

<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
export default {
  name: 'StartCheckCard',
  components: { NavHeaderBanner },
  data() {
    return {
      idCardObject: ''
    }
  },
  methods: {
    nfcClick() {
      console.log(' ')
      window.androidFunUtils.startCheckCard().then((res) => {
        console.log(res)
        this.idCardObject = res
      })
    }
  }
}
</script>

