<template>
  <div>
    <nav-header-banner title="token" />
    <!--    <van-nav-bar title="token" left-text="" left-arrow @click-left="$router.back()" />-->
    <span style="font-size: 16px;color: #666">
      token
    </span>
    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="getToken">token</van-button>
    </div>
    <span></span>
    <div>
      <span>{{ token }}</span>
    </div>

    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="getOfflineToken">token</van-button>
    </div>
    <span></span>
    <div>
      <span>{{ offlineToken }}</span>
    </div>
  </div>
</template>

<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
export default {
  name: 'TokenPage',
  components: { NavHeaderBanner },
  data() {
    return {
      token: '',
      offlineToken: ''
    }
  },
  methods: {
    getToken() {
      window.androidFunUtils.getToken().then((res) => {
        console.log(res)
        this.token = res
      })
    },
    getOfflineToken() {
      window.androidFunUtils.getOfflineToken().then((res) => {
        console.log(res)
        this.offlineToken = res
      })
    }
  }
}
</script>

<style></style>
