<template>
  <div class="page_container">
    <nav-header-banner title="" />
    <!--    <van-nav-bar left-text="" left-arrow @click-left="$router.back()" />-->

    <div
      style=" display: flex; justify-content: center;align-items: center;margin: 18px"
    >
      <van-button type="primary" @click="nfcClick"></van-button>
    </div>
    <span></span>
    <div>
      <div>{{ result1 }}</div>
      <van-button
        v-if="result"
        type="primary"
        @click="playAudio"
      ></van-button>
      <van-button
        v-if="result"
        style="margin: 0 10px"
        type="primary"
        @click="uploadFile"
      ></van-button>
    </div>
    <div>
      <span v-if="!audioStr">{{ progresss }}</span>
      <van-button
        v-else
        type="primary"
        @click="playAudioURL"
      ></van-button>
      <span>{{ audioStr }}</span>
    </div>

  </div>
</template>
<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
import config from '@/config/index'

export default {
  name: 'CaptureVideo',
  components: { NavHeaderBanner },
  data() {
    return {
      result: '',
      result1: '',
      resp: '',
      audioStr: '',
      progresss: 0,
      gateWay: 'http://oort.oortcloudsmart.com:31610'
    }
  },
  mounted() {
    window.androidFunUtils.getGateway().then((res) => {
      console.log(res)
      this.gateWay = res
    })
  },
  methods: {
    playAudioURL() {
      let mediaP = new window.androidFunUtils.OortMedia(this.audioStr)
      mediaP.play()
    },
    playAudio() {
      let mediaP = new window.androidFunUtils.OortMedia(this.result)
      mediaP.play()
    },
    uploadFile() {
      let filePath = this.result
      let server = this.gateWay + '/oort/oortcloud-fastdfsservice/fastdfs/v1/uploadFile'
      let fileKey = 'file'
      let fileName = this.result1[0].name
      let mimeType = this.result1[0].type
      let params = {} // 
      let header = {}
      let httpMethod = 'POST'
      let callback = res => {
        if (res.responseCode && res.responseCode === 200) {
          let rrr = JSON.parse(res.response)
          console.log(rrr.data.url)
          this.audioStr = rrr.data.url
        } else {
          console.log(res)
          if (res.total) {
            this.progresss = res.loaded / res.total * 100 + '%'
          }
        }
      }
      window.androidFunUtils.fileUpload(filePath, server, fileKey, fileName, mimeType, params, header, httpMethod, callback)
    },
    nfcClick() {
      window.androidFunUtils.captureAudio().then((res) => {
        this.result = res[0].fullPath
        this.result1 = res
      })
    }
  }
}
</script>

<style></style>
