<template>
  <div v-if="areaList.length > 1" class="haircut_item_home_1">
    <span class="haircut_item_home_1_span">
      <template v-for="(item,index) in areaList">
        <span v-if="index !== areaList.length - 1" :key="index" style="color: #3157FC" @click="itemDeptClick(index,item)">{{ item.oort_dname }}  /</span>
        <span v-else :key="index + item.oort_dname" style="color: #999999;"> {{ item.oort_dname }}</span>
      </template>
    </span>
  </div>
</template>

<script>
import bus from '@/util/bus'
export default {
  name: 'AreaSelectBar',
  data() {
    return {
      areaList: [
        { oort_dname: '', oort_dcode: '0' }
      ]
    }
  },
  created() {
    bus.$on('addArea', this.addDept)
  },
  methods: {
    addDept(data) {
      this.areaList.push(data)
      // 
      this.$emit('areaClick', data)
    },
    itemDeptClick(index, item) {
      this.areaList = this.areaList.slice(0, index + 1)
      this.$emit('areaClick', item)
    }
  }
}
</script>

<style lang="less" scoped>
  .haircut_item_home_1_borod {
    border-bottom: 0px solid #E5E5E5!important;
  }
  .haircut_item_home_1 {
    cursor: pointer;
    border-bottom: 1px solid #E5E5E5;
    border-top: 1px solid #E5E5E5;
    display: flex;
    padding: 8px 8px;
    justify-content: space-between;
    flex-direction: row;
    background: #fff;
    width: 95%;
    &_span {
      font-size: 14px;
      color: #333333;
    }
  }
</style>
