<template>
  <div>
    <nav-header-banner title="" />
    <!--    <van-nav-bar title="" left-text="" left-arrow @click-left="$router.back()" />-->
    <span style="font-size: 16px;color: #666">
       
    </span>
    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick"></van-button>
    </div>
    <span></span>
    <div>
      <span>{{ result }}</span>
    </div>
  </div>
</template>
