  name: 'BarCodeScanPage',
  components: { NavHeaderBanner },
  data() {
    return {
      result: ''
    }
  },
  methods: {
    nfcClick() {
      window.androidFunUtils.BarcodeScan().then((res) => {
        console.log(res)
        this.result = res
      })
    }
  }
}
</script>

