    >
      js sdk:{{ isOk ? "" : "" }}
    </div>
    <span style="font-weight:bold;margin:8px"> jssdk demo  </span>
    <div class="cell_body">
      <van-cell value="NFC" is-link to="/nfc" />
      <van-cell value="OCR" is-link to="/ocr" />
      <van-cell value="app" is-link to="/item" />
      <van-cell value="" is-link to="/codescan" />
      <van-cell value="" is-link to="/appversion" />
      <van-cell value="" is-link to="/debug" />
      <van-cell value="" is-link to="/gateway" />
      <van-cell value="token" is-link to="/token" />
      <van-cell value="" is-link to="/offLineApplist" />
      <van-cell value="" is-link to="/appExit" />
      <van-cell value="" is-link to="/headInfo" />
      <van-cell value="" is-link to="/ajax" />
      <van-cell value="" is-link to="/gohome" />
      <van-cell value="wps" is-link to="/openwps" />
      <van-cell value="" is-link to="/openDefault" />
      <van-cell value="" is-link to="/takePhoto" />
      <van-cell value="" is-link to="/captureVideo" />
      <van-cell value="" is-link to="/captureAudio" />
      <van-cell value="" is-link to="/setMessageToUser" />
