</template>

<script>
import NavHeaderBanner from '../components/navHeaderBanner/index'
import config from '@/config/index'

export default {
  name: 'Home',
  components: { NavHeaderBanner },
  data() {
    return {
      isOk: false
    }
  },
  created() {
    document.addEventListener('deviceready', this.init, false)
  },
  methods: {
    init() {
      this.isOk = true
      window.androidFunUtils.getGateway().then((res) => {
        config.URL = res
      })
      window.androidFunUtils.getToken().then((res) => {
        config.token = res
      })
    }
  }
}
</script>

<style scoped>
