<template>
  <div>
    <nav-header-banner title="" />
    <!--    <van-nav-bar left-text="" left-arrow @click-left="$router.back()" />-->

    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick1"></van-button>
    </div>
    <span></span>
    <div>
      <img style="width: 80px" :src="result1">
    </div>

    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick"></van-button>
    </div>
    <span></span>
    <div>
      <img style="width: 80px" :src="result">
    </div>

    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="nfcClick2">captureImage</van-button>
    </div>
    <span></span>
    <div>
      <span>{{ result2 }}</span>
    </div>
  </div>
</template>
<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
import config from '@/config/index'

export default {
  name: 'OCRPage',
  components: { NavHeaderBanner },
  data() {
    return {
      result: '',
      result1: '',
      result2: ''
    }
  },
  methods: {
    nfcClick2() {
      window.androidFunUtils.captureImage().then((res) => {
        console.log(res[0])
        this.result2 = res
      })
    },
    nfcClick1() {
      let obj = {
        mQuality: 10, // 0-100  0 low  100 high
        destType: 1, // 0 base64   12 file uri
        srcType: 1, // 0 1 2 
        targetWidth: 0, // 0
        targetHeight: 0, //  0
        encodingType: 1, //    0 jpeg , 1 png
        mediaType: 0, // 0  1  2 
        allowEdit: false, // 0  1 
        correctOrientation: false, // 0  1 
        saveToPhotoAlbum: true // 0  1 
      }
      window.androidFunUtils.takePhoto(obj).then((res) => {
        console.log(res)
        let fileURL = res.split('?')[0]
        let ft = new window.FileTransfer()
        let options = new window.FileUploadOptions()
        console.log(options)
        // 
        options.fileName = fileURL.substr(fileURL.lastIndexOf('/') + 1)
        console.log(config.URL)
        let uri = config.URL + 'oort/oortcloud-fastdfsservice/fastdfs/v1/uploadFile'
        console.log(config.URL)
        ft.upload(
          fileURL,
          encodeURI(uri),
          (msg) => {
            console.log(msg)
            try {
              let res = JSON.parse(msg.response)
              if (res.code === 200) {
                this.result = res.data.url
                // this.upImage(res.data.url)
              }
            } catch (e) {
              alert(' uploadFile')
            }
          },
          (err) => {
            console.log(err)
          },
          options
        )
      })
    },
    nfcClick() {
      let obj = {
        mQuality: 0, // 0-100  0 low  100 high
        destType: 1, // 0 base64   12 file uri
        srcType: 2, // 0 1 2 
        targetWidth: 0, // 0
        targetHeight: 0, //  0
        encodingType: 1, //    0 jpeg , 1 png
        mediaType: 0, // 0  1  2 
        allowEdit: false, // 0  1 
        correctOrientation: false, // 0  1 
        saveToPhotoAlbum: true // 0  1 
      }
      window.androidFunUtils.takePhoto(obj).then((res) => {
        console.log(res)
        let fileURL = res.split('?')[0]
        let ft = new window.FileTransfer()
        let options = new window.FileUploadOptions()
        console.log(options)
        // 
        options.fileName = fileURL.substr(fileURL.lastIndexOf('/') + 1)
        console.log(config.URL)
        let uri = config.URL + 'oort/oortcloud-fastdfsservice/fastdfs/v1/uploadFile'
        console.log(config.URL)
        ft.upload(
          fileURL,
          encodeURI(uri),
          (msg) => {
            console.log(msg)
            try {
              let res = JSON.parse(msg.response)
              if (res.code === 200) {
                this.result = res.data.url
                // this.upImage(res.data.url)
              }
            } catch (e) {
              alert(' uploadFile')
            }
          },
          (err) => {
            console.log(err)
          },
          options
        )
      })
    }
  }
}
</script>

<style></style>
