<template>
  <div>
<!--    <van-nav-bar title="" left-text="" left-arrow @click-left="$router.back()" />-->
    <nav-header-banner title=""></nav-header-banner>
    <span style="font-size: 16px; color: #666">
      jssdk
    </span>
    <div style="display: flex; justify-content: center; align-items: center; margin: 18px">
      <van-button type="primary" @click="getUserInfoAjax"></van-button>
    </div>
    <span></span>
    <div style="width: 100%">
      {{ result }}
    </div>
  </div>
</template>

<script>
import NavHeaderBanner from "@/components/navHeaderBanner/index";
export default {
  name: 'AjaxPage',
  components: {NavHeaderBanner},
  data() {
    return {
      result: ''
    }
  },
  methods: {
    // 
    getUserInfoAjax() { 
      //  token
      window.androidFunUtils.getGateway().then((res) => {
        let gateWayURL = res
        window.androidFunUtils.getToken().then((res1) => {
          let token = res1
          let url = gateWayURL + 'oort/oortcloud-sso/' + 'sso/v1/getUserInfo'
          const params = {
            data: {
              accessToken: token
            },
            url: url,
            method: 'post'
          }
          console.log('http')
          window.androidFunUtils.http(params).then(
            (ress) => {
              console.log(ress)
              this.result = ress
            },
            (reject) => {
              alert(reject)
            }
          )
        })
      })
    }
  }
}
</script>

