import { createRouter, createWebHashHistory } from 'vue-router'

import Home from '../page/home.vue'
import NfcPage from '../page/nfcPage.vue'
import AjaxPage from '../page/ajaxPage.vue'
import AppversionPage from '../page/appversionPage.vue'
import BarCodeScanPage from '../page/barCodeScanPage.vue'
import ConfigPage from '../page/configPage.vue'
import DebugPage from '../page/debugPage.vue'
import GatewayPage from '../page/gatewayPage.vue'
import HeadInfoPage from '../page/headInfoPage.vue'
import OffLineAppPage from '../page/offLineAppPage.vue'
import TokenPage from '../page/tokenPage.vue'
import OcrPage from '../page/ocrPage.vue'
import appExit from '../page/appExitPage.vue'
import GoHome from '../page/gohomePage.vue'
import WpsHome from '../page/openWps.vue'
import DefaultBro from '../page/defaultBro'
import TakePhoto from '../page/takephoto'
import CaptureVideo from '../page/captureVideo'
import CaptureAudio from '../page/captureAudio'
import SetMessageToUser from '../page/setMessageToUser'
import SelfMeunAndAuthLogin from '../page/selfMeunAndAuthLogin'
import StartCheckCard from '../page/startCheckCard'
import GetLocation from '../page/getLocation.vue'

import About from '@/components/commonpage/aboutPage'
import ContactUS from '@/components/commonpage/contactUS'
import VersionsPage from '@/components/commonpage/versionsPage'

// 2. 
// 
// 
const routes = [
  { path: '/', component: Home },
  { path: '/ocr', component: OcrPage },
  { path: '/item', component: ConfigPage },
  { path: '/codescan', component: BarCodeScanPage },
  { path: '/appversion', component: AppversionPage },
  { path: '/debug', component: DebugPage },
  { path: '/gateway', component: GatewayPage },
  { path: '/token', component: TokenPage },
  { path: '/offLineApplist', component: OffLineAppPage },
  { path: '/appExit', component: appExit },
  { path: '/headInfo', component: HeadInfoPage },
  { path: '/ajax', component: AjaxPage },
  { path: '/nfc', component: NfcPage },
  { path: '/goHome', component: GoHome },
  { path: '/openwps', component: WpsHome },
  { path: '/openDefault', component: DefaultBro },
  { path: '/takePhoto', component: TakePhoto },
  { path: '/captureVideo', component: CaptureVideo },
  { path: '/captureAudio', component: CaptureAudio },
  { path: '/setMessageToUser', component: SetMessageToUser },
  { path: '/selfMeunAndAuthLogin', component: SelfMeunAndAuthLogin },
  { path: '/startCheckCard', component: StartCheckCard },
  { path: '/getLocation', component: GetLocation },

  {
    path: '/aboutAppPage',
    name: 'aboutAppPage',
    component: About,
    meta: {
      index: 101,
      title: ''
    }
  },
  {
    path: '/contactUS',
    name: 'ContactUS',
    component: ContactUS,
    meta: {
      index: 102,
      title: ''
    }
  },
  {
    path: '/versionsPage',
    name: 'versionsPage',
    component: VersionsPage,
    meta: {
      index: 102,
      title: ''
    }
  }
]

const router = createRouter({
  // 4.  history  hash 
  history: createWebHashHistory(),
  routes // `routes: routes` 
})

export default router
