<template>
  <div>

    <nav-header-banner title="NFC" />
    <div
      style="
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 18px;
      "
    >
      <van-button type="primary" @click="nfcClick">NFC</van-button>
    </div>

    <div v-if="idCardObject.name">
      <van-cell-group>
        <van-field
          v-model="idCardObject.name"
          label=""
          placeholder=""
        />
        <van-field
          v-model="idCardObject.gender"
          label=""
          placeholder=""
        />
        <van-field
          v-model="idCardObject.ethnicity"
          label=""
          placeholder=""
        />
        <van-field
          v-model="idCardObject.birth"
          label=""
          placeholder=""
        />
        <van-field
          v-model="idCardObject.address"
          label=""
          placeholder=""
        />
        <van-field
          v-model="idCardObject.cardNo"
          label=""
          placeholder=""
        />
        <van-field
          v-model="idCardObject.authority"
          label=""
          placeholder=""
        />
        <van-field
          v-model="idCardObject.period"
          label=""
          placeholder=""
        />
      </van-cell-group>
      <div
        style="
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 18px;
        "
      >
        <van-image
          width="90px"
          height="90px"
          fit="contain"
          :src="'data:image/png;base64,' + idCardObject.pictureBase64"
        />
      </div>
    </div>
  </div>
</template>
<script>
import NavHeaderBanner from '@/components/navHeaderBanner/index'
export default {
  name: 'NfcPage',
  components: { NavHeaderBanner },
  data() {
    return {
      idCardObject: {
        name: '', // 
        address: '', // 
        birth: '', // 
        cardNo: '', // 
        ethnicity: '', // 
        startDate: '', //  eg: 2010.09.16
        endDate: '', //  eg: 2020.09.16
        period: '', //  "2018.09.17-2038.09.17"
        gender: '', //  eg: ''
        authority: '', // 
        pictureBase64: '', // base64
        UID: '',
        originalString: ''
      }
    }
  },
  methods: {
    nfcClick() {
      window.androidFunUtils.readIDCard().then((res) => {
        console.log(res)
        this.idCardObject = res
      })
    }
  }
}
</script>

