<template>
  <div>
    <nav-header-banner title="App"/>
    <span style="font-size: 16px;color: #666">
      h5app ,
    </span>
    <span style="font-size: 16px;color: red">
       
    </span>

    <van-cell-group title="">
      <van-field v-model="key" label="key" placeholder="key" />
      <van-field v-model="value" label="value" placeholder="" />
      <van-button type="primary" style="width:100%" @click="saveValue"></van-button>
    </van-cell-group>
    <div style="height:32px" />
    <van-cell-group>
      <van-field v-model="key" label="key" placeholder="key" />
      <van-field v-model="result" label="vaule" disabled placeholder="key" />
      <van-button type="primary" style="width:100%" @click="getValue"></van-button>
    </van-cell-group>

  </div>
</template>
<script>
import { Toast } from 'vant'
import NavHeaderBanner from "@/components/navHeaderBanner/index";
export default {
  name: 'ConfigPage',
  components: {NavHeaderBanner},
  data() {
    return {
      key: 'test',
      value: 'test',
      result: ''
    }
  },
  methods: {
    saveValue() {
      window.androidFunUtils.setItem(this.key, this.value)
    },
    getValue() {
      window.androidFunUtils.getItem(this.key).then(res => {
        console.log(res)
        Toast.success('', res)
        this.result = res
      })
    }
  }
}
</script>

