import { createApp } from 'vue'

import App from './App.vue'

import router from './router/index'

const VueApp = createApp(App)

// 
import {
  Button,
  Cell,
  Field,
  Image,
  CellGroup,
  NavBar,
  Icon,
  Dialog,
  Popup,
  Picker
} from 'vant'

VueApp.use(Picker)
VueApp.use(Popup)
VueApp.use(Dialog)
VueApp.use(Icon)
VueApp.use(Button)
VueApp.use(Cell)
VueApp.use(Field)
VueApp.use(Image)
VueApp.use(CellGroup)
VueApp.use(NavBar)
VueApp.use(router)
VueApp.mount('#app')
